/* mobile first */

.work {
  background: white;
  display: flex;
  border-radius: 10px;
  overflow:hidden;
}

.d-work {
  flex-direction: row;
  width: 800px;
  height: 350px;
}

.m-work {
  flex-direction: column;
  width: 330px;
}

.drawers {
  height: 350px;
  width: auto;
  margin-bottom: 0;
  clip: rect(0, 350px, 350px, 0);
}

img {
  margin-bottom: 15px;
}

@font-face {
    font-family: "Manrope-B";
    src: url("./fonts/Manrope/static/Manrope-Bold.ttf");
}

.bold {
  font-weight: 700;
  margin: 30px 42px;
  line-height: 24px;
  font-family: "Manrope-B";
  font-size: 15px;
}

.light {
  margin: 0 42px 30px 42px;
}

.foot {
  display: flex;
  flex-direction: row;
  margin: 0 42px 30px 42px;
  align-items: center;
  justify-content: space-between;;
}

.profile {
  display: flex;
  flex-direction: column;
}

.name {
  font-family: "Manrope-B"
}

.face {
  height: 45px;
  width: 45px;
  border-radius: 50px;
  margin-right: 15px;
  margin-bottom: 0;
}

.left{
  display: flex;
}

.shareBorder {
  padding: 10px;
  background:hsl(210, 46%, 95%);
  border-radius: 50px;
}

.share {
  margin: 0;
}

.share-text {
  color: hsl(212, 23%, 69%);
  letter-spacing: 4px;
}

.active {
  display: flex;
  flex-direction: row;
  background: hsl(217, 19%, 35%);
  justify-content: space-between;
  align-items:center;
  height: 75px;
}

.shareBorderA {
  padding: 10px;
  background:hsl(214, 17%, 51%);
  border-radius: 50px;
  margin-right: 42px;
}

.leftA {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 42px;
}

.shareIcon {
  margin: 8px;
}

.shareIcons {
  display: flex;
  margin-left: 15px;
}

.nonImage {
  width: 647.5px;
  padding: 30px;
  margin-left:-85px;
  background: white;
}

.popup {
  position: absolute;
}
